import GeoJSON from 'ol/format/GeoJSON';
import Map from 'ol/Map';
import VectorLayer from 'ol/layer/Vector';
import VectorSource from 'ol/source/Vector';
import View from 'ol/View';
import DragAndDrop from 'ol/interaction/DragAndDrop';
import sync from 'ol-hashed';
import TileLayer from 'ol/layer/Tile';
import OSM from 'ol/source/OSM';
import {Style, Fill, Stroke} from 'ol/style';
import {Circle as CircleStyle, Fill, Stroke, Style} from 'ol/style';
import Feature from 'ol/Feature';
import Point from 'ol/geom/Point';
import {circular} from 'ol/geom/Polygon';
import {fromLonLat} from 'ol/proj';
import Control from 'ol/control/Control';
import {ZoomToExtent, defaults as defaultControls} from 'ol/control';
import MousePosition from 'ol/control/MousePosition';
import {createStringXY} from 'ol/coordinate';
import { labelCache } from 'ol/render/canvas';


const mousePositionControl = new MousePosition({
  coordinateFormat: createStringXY(4),
  projection: 'EPSG:3857',
  // comment the following two lines to have the mouse position
  // be placed within the map.
  className: 'custom-mouse-position',
  target: document.getElementById('mouse-position'),

});



const zeltplaetzeStyle = new CircleStyle({
  radius: 5,
  fill: new Fill({ color: 'white'}),
  stroke: new Stroke({color: 'black', width: 2}),
});

const supermarktStyle = new CircleStyle({
  radius: 5,
  fill: new Fill({ color: 'red'}),
  stroke: new Stroke({color: 'black', width: 2}),
});

const baeckerStyle = new CircleStyle({
  radius: 5,
  fill: new Fill({ color: 'orange'}),
  stroke: new Stroke({color: 'black', width: 2}),
});
const routeStyle = new Style ({
  stroke: new Stroke({
    color: 'black',
    width: 4,
  }),
})



const map = new Map({
  target: 'map-container',



  controls: defaultControls().extend([mousePositionControl,
    new ZoomToExtent({
      extent: [
        785195.0136 , 6560234.5747 ,
        877545.9838 , 6683103.5763
      ],
      className:'Tag1',
      label:'1'
      }),  
      new ZoomToExtent({
        extent: [
        866575.8137,  6675546.9273,
        977882.7383,  6791269.6796
      ],
      className:'Tag2',
      label:'2'   
    }),
    new ZoomToExtent({
      extent: [
        975248.0143,  6787858.9786,
        1099697.9325, 6884029.4896
    ],
    className:'Tag3',
    label:'3'
    }),
    new ZoomToExtent({
      extent: [
        1090930.1618, 6846690.7229,
        939361.1667,  7028162.5925
    ],
    className:'Tag4',
    label:'4'
    }),
    new ZoomToExtent({
      extent: [
        962712.8673,  6979188.8870,
        1143833.1248, 7116713.6817

    ],
    className:'Tag5',
    label:'5'
    }), 
    new ZoomToExtent({
      extent: [
        1086162.1211, 7061765.9149,
        1149516.3230, 7113169.2186
    ],
    className:'Tag6',
    label:'6'
    }),  
    new ZoomToExtent({
      extent: [
        1091940.4568, 7060280.4052,
        1221347.7977, 7179309.2125
    ],
    className:'Tag7',
    label:'7'
    }),
    new ZoomToExtent({
      extent: [
        1208073.9457, 7165227.4561,
        1212845.4349, 7159011.3147
    ],
    className:'Tag8',
    label:'8'
    }),
  ]),
  

  layers: [

    new TileLayer({
      source: new OSM(),
    }),

    new VectorLayer({
      source: new VectorSource({
        format: new GeoJSON(),
        url:'./data/Route.geojson',
      }),
      style:routeStyle
    }),


    new VectorLayer({
      source: new VectorSource({
        format: new GeoJSON(),
        url:'./data/Zeltplaetze.geojson', 
      }),
        style:new Style({
        image:zeltplaetzeStyle
        })
      }),

    new VectorLayer({
      source: new VectorSource({
        format: new GeoJSON(),
        url:'./data/Supermarkt.geojson',
      }),
      style:new Style({
        image:supermarktStyle
      })   
    }),

    new VectorLayer({
      source: new VectorSource({
        format: new GeoJSON(),
        url:'./data/Baeckereien.geojson',
      }),
      style:new Style({
        image:baeckerStyle
      })
    }),    
  ],


  view: new View({
    center: [0, 0],
    zoom: 2,
  }),
});

const source = new VectorSource();
const layer = new VectorLayer({
  source: source,
});
map.addLayer(layer);

map.addInteraction(
  new DragAndDrop({
    source: source,
    formatConstructors: [GeoJSON],
  })
);

navigator.geolocation.watchPosition(
  function (pos) {
    const coords = [pos.coords.longitude, pos.coords.latitude];
    const accuracy = circular(coords, pos.coords.accuracy);
    source.clear(true);
    source.addFeatures([
      new Feature(
        accuracy.transform('EPSG:4326', map.getView().getProjection())
      ),
      new Feature(new Point(fromLonLat(coords))),
    ]);
  },
  function (error) {
    alert(`ERROR: ${error.message}`);
  },
  {
    enableHighAccuracy: true,
  }
);

const locate = document.createElement('div');
locate.className = 'ol-control ol-unselectable locate';
locate.innerHTML = '<button title="Locate me"></button>';
locate.addEventListener('click', function () {
  if (!source.isEmpty()) {
    map.getView().fit(source.getExtent(), {
      maxZoom: 18,
      duration: 500,
    });
  }
});
map.addControl(
  new Control({
    element: locate,
  })
);

sync(map);
